:root {
    // Основний колір бекгранду
    --primary-background-color: #fef3e4;

    //   Основний колір тексту (загаловки, абзаци)
    --main-text-color: #2e2520;
    //  Колір кнопок
    --button-color: #ab816c;
    // Колір кнопок при ховері
    --hover-button-color: #674d40;
}

html {
    scroll-behavior: smooth;
}

ul,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

.list {
    list-style: none;
}

.link {
    text-decoration: none;
    color: inherit;
}

.container {
    min-width: 320px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (max-width: 767px) {
        padding-left: 15px;
        padding-right: 15px;
    }

    @media screen and (max-width: 767px) {
        width: 320px;
    }

    @media screen and (min-width: 767.98px) {
        width: 634px;
    }

    @media screen and (min-width: 1279.98px) {
        width: 822px;
    }
}
